const defaultData = {
  startDate: '01/07/2020',
  endDate: '01/07/2022'
}

const readonly = [
  'cnpjCliente',
  'cnpjEmpresaDeAtendimento',
  'nomeProduto',
  'codProduto',
  'engenheiroTxt',
  'responsavelPelaExecucaoDoServico'
]

const h = {
  $inp (id) {
    return document.querySelector(`[xname=inp${id}]`)
  },

  addYears (date, quantity) {
    const newDate = new Date(date)
    newDate.setFullYear(date.getFullYear() + quantity)

    return newDate
  },

  addMonths (date, quantity) {
    const newDate = new Date(date)
    newDate.setMonth(date.getMonth() + quantity)

    return newDate
  },

  formatDate (date) {
    return (new Date(date)).toLocaleDateString('pt-BR')
  }
}

function fillData (e) {
  e.preventDefault()

  const $contactsTable = document.querySelector('#tbl-contacts')
  const productType = this.dataset.product
  const data = {
    ...defaultData,
    ...specificData[productType]
  }

  appendData(
    $contactsTable,
    [{
      contato: 'Alessany Marques',
      emailEmpresa: 'analista.rh@olpapeis.teste.br'
    }, {
      contato: 'Valdecir Roberto Bechel',
      emailEmpresa: 'administrativo@olpapeis.teste.br'
    }, {
      contato: 'Anderson S.',
      emailEmpresa: 'auxiliar.qualidade@olpapeis.com.br'
    }, {
      contato: 'Luciana Carvalho',
      emailEmpresa: 'treinamento@olpapeis.com.br'
    }, {
      contato: 'Pablo Josué',
      emailEmpresa: 'seguranca@olpapeis.com.br'
    }]
  )

  // DADOS DO CONTATO
  h.$inp('numeroDaProposta').value = data.contract
  h.$inp('razaoSocial').value = data.razaoSocial
  h.$inp('cnpjCliente').value = data.cnpj
  h.$inp('inicioContrato').value = data.startDate
  h.$inp('validadeDaProposta').value = data.endDate

  // DADOS DO PRODUTO
  h.$inp('nomeProduto').value = data.productName
  h.$inp('empresaDeAtendimento').value = data.razaoSocial
  h.$inp('cnpjEmpresaDeAtendimento').value = data.cnpj
  h.$inp('contaContabil').value = data.account
  h.$inp('centroResultado').value = data.resultCenter

  // VARIÁVEIS
  h.$inp('codProduto').value = data.productId
  h.$inp('codigoUnidadeExecutanteOrquestra').value = data.unityId
}

function setEndDate () {
  const currDate = $(this).datepicker('getDate')
  const endDate = h.addYears(currDate, 2)

  h.$inp('validadeDaProposta').value = h.formatDate(endDate)
}

function setStartDate () {
  const currDate = $(this).datepicker('getDate')
  const startDate = h.addYears(currDate, -2)

  h.$inp('inicioContrato').value = h.formatDate(startDate)
}

function setReadOnly (fields) {
  fields.forEach(field => {
    const $field = h.$inp(field)

    $field.classList.add('-readonly')

    $field.addEventListener('focus', function () {
      this.blur()
    })
  })
}

function mount () {
  $(h.$inp('inicioContrato')).on('change', setEndDate)
  $(h.$inp('validadeDaProposta')).on('change', setStartDate)
}

function init () {
  mount()
  setReadOnly(readonly)

  document.querySelectorAll('.js-fill')
    .forEach(btn => btn.addEventListener('click', fillData))
}

init()
